.expression-container .react-resizable.height-based-on-content {
  /**
    It seems that react-resizable is always expecting an height, even if we are interested in having 100% for it.
    Since react-resizable specifies inline style, the only way to overwrite it, was to assign a specific class (height-based-on-content)
    with the !important card
    */
  height: 100% !important;
  position: relative;
}

.expression-container .pf-c-drawer__splitter::after {
  border: none;
}

.expression-container .pf-c-drawer .pf-c-drawer__splitter-handle::after {
  border-left: none;
  position: absolute;
  left: -1px;
  top: 50%;
  border-color: var(--pf-global--palette--black-300);
  transform: translateY(-50%);
}

.expression-container
  .react-resizable
  table
  td:last-child
  > .react-resizable
  > .pf-c-drawer
  .pf-c-drawer__splitter-handle::after,
.expression-container
  .react-resizable
  table
  th:last-child
  > .react-resizable
  > .pf-c-drawer
  .pf-c-drawer__splitter-handle::after {
  opacity: 0;
}
